#root{
    width: 100%;
    padding-bottom: 6vh;
    padding-top: 20px;
    display: grid; 
    grid-template-columns: 1fr 15em 15em 15em 1fr; 
    grid-template-rows: 300px auto ; 
    gap: 30px;
    background-image: url('../../../public/multimedia/tlo-tyl1-czarne.png');
    background-attachment: fixed;
}

.showAndHideNavButton{
    display: none;
}

header{
    grid-area: 1 / 2 / 2 / 5 ; 
} 
nav{
    position: sticky;
    grid-area: 2 / 2 / 3 / 3;
    position: -webkit-sticky;
    top:5vh;
    height: 90vh;
    padding-right: 0;
}
main{
    background-color: rgb(231, 209, 159);
    grid-area: 2 / 3 / 3 / 5;
}

@media (min-width: 90rem){
    #root{
        grid-template-rows: 400px auto; 
        grid-template-columns: 1fr 25em 25em 25em 1fr; 
    }
}

@media (max-width: calc(50rem + 60px)){
    #root{
        grid-template: 200px auto / 1fr 10rem 10rem 10rem 1fr; ; 
        min-width: min-content;
        position: relative;
    }
    nav{
        width:calc( max-content + 300px);
        height: 90vh;
        top:30px;
        position: fixed;
        padding-left: 390px;
        left: -350px;
        z-index: 2;
    }
    main {
        grid-area: 2 / 2 / 3 / 5
    }
    header {
        grid-area: 0 / 2 / 1 / 3
    }
    .hiddenNav{
        left: -100em;
    }
    .showAndHideNavButton{
        display: block;
    }
}
@media (max-width: 40em) {
    #root{
        grid-template: 200px auto / 1fr repeat(3 , max(20% , 85px)) 1fr; 
        min-width: 400px;
    }
  }